// src/pages/SearchPage.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import ApiTraderServices from '../services/ApiTraderServices';
import DateRangePicker from '../components/DateRangePicker'; 
import DateUtils from '../utils/DateUtils'; 
import './css/SearchPage.css';

const SearchPage = () => {
  const [query, setQuery] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  const handleSearch = async (e) => {
    e.preventDefault();
    if (query) {
      setIsLoading(true); // Exibir indicador de carregamento
      try {
        const results = await ApiTraderServices.searchStock(query); // Usar ApiTraderServices
        setIsLoading(false); // Ocultar indicador de carregamento
        navigate(`/results?query=${query}`, { state: { results } }); // Passar os resultados para a página de resultados
      } catch (error) {
        console.error('Erro ao pesquisar:', error);
        setIsLoading(false); // Em caso de erro, ocultar indicador de carregamento
      }
    }
  };

  // Função para lidar com seleções de intervalo de data
  const handleDateRange = (startDate, endDate) => {
    // Implementar lógica para lidar com a seleção de intervalo de datas
    console.log(`Selecionado intervalo de datas: ${startDate} - ${endDate}`);
  };

  // Função para lidar com seleção de data personalizada
  const handleCustomDate = (startDate, endDate) => {
    if (startDate && endDate) {
      handleDateRange(startDate, endDate);
    }
  };

  return (
    <div className="search-page">
      <h1>Buscador de Ações</h1>
      <form onSubmit={handleSearch}>
        <input
          type="text"
          placeholder="Digite o ticker da ação"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          disabled={isLoading} // Desabilitar o campo de entrada durante o carregamento
        />
        <button type="submit" disabled={isLoading}>
          {isLoading ? 'Buscando...' : 'Buscar'}
        </button>
      </form>
      {isLoading && <p>Carregando...</p>} {/* Exibir indicador de carregamento */}
      
      {/* Botões de seleção de intervalo de datas */}
      <div className="date-buttons">
        <button onClick={() => handleDateRange(DateUtils.getTodayDate(), DateUtils.getTodayDate())}>Hoje</button>
        <button onClick={() => handleDateRange(DateUtils.getStartDateOfWeek(DateUtils.getTodayDate()), DateUtils.getTodayDate())}>Esta Semana</button>
        <button onClick={() => handleDateRange(DateUtils.getStartDateOfMonth(DateUtils.getTodayDate()), DateUtils.getTodayDate())}>Este Mês</button>
        <button onClick={() => handleDateRange(DateUtils.getStartDateOfYear(DateUtils.getTodayDate()), DateUtils.getTodayDate())}>Este Ano</button>
        <button onClick={() => handleCustomDate()}>Personalizado</button>
      </div>
      
      {/* Componente DateRangePicker para seleção de datas personalizadas */}
      <div className="custom-date-picker">
        <DateRangePicker onChange={handleCustomDate} />
      </div>
    </div>
  );
};

export default SearchPage;
